import locales from './locales.json'
const defaultConfig = {
  color: '#333',
  tipPosition: 'top',
  tipTextColor: '#f2f2f2',
  tipBackgroundColor: '#4b667d',
  tipShadowColor: 'rgba(0,0,0,.1)',
  interactiveMode: 'press',
  returnMode: 'increment',
  language: 'zh_cn',
  vad_eos: 3000
}
export default {
  props: {
    color: String,
    tipPosition: String,
    appId: String,
    apiKey: String,
    apiSecret: String,
    // 
    // 'press': 
    // 'touch': 
    interactiveMode: String,
    // 
    // 'increment': 
    // 'complete': 
    returnMode: String,
    language: String,
    accent: String,
    pd: String,
    rlang: String,
    ptt: Number,
    nunum: Number,
    vad_eos: Number
  },
  methods: {
    getConfig (key) {
      if (!this.IATConfig) this.IATConfig = {}
      return this[key] || this.IATConfig[key] || defaultConfig[key]
    }
  },
  computed: {
    pressMode () {
      return this.getConfig('interactiveMode') !== 'touch'
    },
    touchMode () {
      return this.getConfig('interactiveMode') === 'touch'
    },
    incrementMode () {
      return this.getConfig('returnMode') === 'increment'
    },
    completeMode () {
      return this.getConfig('returnMode') === 'complete'
    },
    locale () {
      const locale = locales[this.getConfig('language')]
      return locale
    }
  }
}
